.boasting {
  box-sizing: border-box;
  width: 100vw;
  background-color: @background-light;
  margin: 0;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-right: 20px;
  padding-left: 20px;

  color: @text-color-blue;
}

.boasting__list {
  list-style-type: none;
  padding: 0;
}


.boasting__list-element {
  box-sizing: border-box;
  width: 100%;
  text-align: center;
}


